body {
  font-family: "Ubuntu", sans-serif;
  height: 98vh;
  #root, .App {
    height: 100%;
  }
}
.navbar {
  span {
    color: #fff !important;
  }
  .nav-link {
    font-size: larger;
    font-weight: 500;
  }
  .navbar-brand {
    font-size: 3rem;
    font-weight: bold;
  }
}
.homeSection {
  // height: 86.1vh;
  border-radius: 1em 1em;
  padding-left: 200px;
  padding-right: 200px;
  padding-top: 50px;
  // display: flex;
  // flex-direction: column;
  // justify-content: flex-start;

  h3 {
    font-size: 2.5em;
    line-height: 1.7em;
    text-transform: uppercase;
  }
  h1 {
    font-size: 4.5em;
    // line-height: 1.3em;
    font-weight: bold;
    letter-spacing: 3px;
  }

  .secondSection {
    justify-content: center;
    display: flex;
    margin-bottom: 3rem;
    img {
      width: 100%;
    }
  }

  .firstSection {
    text-align: left;
  }
}
.aboutAvatar {
  width: 200px;
  height: 200px;
  // border: 1px solid black;
  border-radius: 100%;
}
.goodAt {
  font-size: x-large;
  .technologies span {
    white-space: nowrap;
  }
  .technologies:first-child {
    animation: skill1 1.3s ease forwards;
  }
  @keyframes skill1 {
      from { width: 0%; }
      to { width: 70%; }
  }


  .technologies:nth-child(2) {
    animation: skill2 1.6s ease forwards;
  }
  @keyframes skill2 {
      from { width: 0%; }
      to { width: 78%; }
  }

  .technologies:nth-child(3) {
    animation: skill3 1.9s ease forwards;
  }
  @keyframes skill3 {
      from { width: 0%; }
      to { width: 75%; }
  }

  .technologies:nth-child(4) {
    animation: skill4 2.2s ease forwards;
  }
  @keyframes skill4 {
      from { width: 0%; }
      to { width: 85%; }
  }

  .technologies:nth-child(5) {
    animation: skill5 2.5s ease forwards;
  }
  @keyframes skill5 {
      from { width: 0%; }
      to { width: 80%; }
  }

  .technologies:nth-child(6) {
    animation: skill6 2.8s ease forwards;
  }
  @keyframes skill6 {
      from { width: 0%; }
      to { width: 70%; }
  }

  .technologies:nth-child(7) {
    animation: skill7 3.1s ease forwards;
  }
  @keyframes skill7 {
      from { width: 0%; }
      to { width: 60%; }
  }

}
.mainContainer {
  max-width: 1920px;
  height: 100%;
  border-radius: 0.8em;
  box-shadow: 0 0.5em 1em 1em rgb(0 0 0 / 50%);
  background-image: url("../background.jpeg");
  background-size: cover;
  color: #fff;
  // backdrop-filter: blur(5px);
  border-radius: 1em;
}
.content {
  backdrop-filter: blur(5px);
  border-radius: 0em 0em 1em 1em;
  height: calc(100% - 28px);
}
.terminal-header {
  background: #000;
  border-radius: 0.7em 0.7em 0 0;
}
.terminal-header .terminal-header-buttons {
  display: flex;
  flex-direction: row;
  padding: 0.5em 1em;
}
.terminal-header .terminal-header-buttons .terminal-button.close {
  background: #ff4746;
  border: 1px solid #b81717;
  position: relative;
}
.terminal-header .terminal-header-buttons .terminal-button {
  width: 0.75em;
  height: 0.75em;
  border-radius: 1em;
  margin-right: 0.5em;
}
.terminal-header .terminal-header-buttons .terminal-button.close:after {
  content: "";
  width: 0.25em;
  height: 0.25em;
  position: absolute;
  top: 0.1875em;
  left: 0.1875em;
  background: #b81717;
  border-radius: 1em;
}
.terminal-header .terminal-header-buttons .terminal-button.minimise {
  background: #ffbe00;
  border: 1px solid #aa7813;
}
.terminal-header .terminal-header-buttons .terminal-button {
  width: 0.75em;
  height: 0.75em;
  border-radius: 1em;
  margin-right: 0.5em;
}
.terminal-header .terminal-header-buttons .terminal-button.expand {
  background: #00db47;
  border: 1px solid #0d791f;
}
.terminal-header .terminal-header-buttons .terminal-button {
  width: 0.75em;
  height: 0.75em;
  border-radius: 1em;
  margin-right: 0.5em;
}
.downloadResumeBtn {
  background-color: tomato;
  color: #fff;
  font-size: 1.5rem;
  border-radius: 0rem 0rem 0.7rem 0rem;
}
.aboutModal {
  color: #000;
  .aboutHighlighter {
    color: #000;
    border-bottom: 2px solid #000;
  }
}

.avatar {
  width: 300px;
  height: 300px;
  border: 1px solid white;
  border-radius: 100%;
}
